/**
 * menu 
 *
 * @prop store - 
 * @prop noCoverTrig - TODO pc mobile 
 * @prop noTrig - 
 * @prop height -  auto
 *                1auto
 *                2
 * @prop width -  170
 *               1auto trigger 
 *               2
 * @prop trigHeight -  auto
 *                    1auto
 *                    2
 *
 * @event afterSpread - 
 * @event afterFold - 
 * @event scrollerChange - 
 */

import './Menu.scss'

import Vue from 'vue'

import render from './Menu.render'
import store from '../../vuex/store'
import hubStore from '../../vuex/module/hub/type.json'
import compStore from '../../vuex/module/comp/type.json'
import tip from '../Message/tip'

import Btn from '../Btn/Btn'
import Icon from '../Icon/Icon'
import Input from '../Input/Input'
import Motion from './Motion'
import Scroller from '../Scroller/Scroller'

import baseMixin from '../../mixin/base'
import formMixin from '../../mixin/form'
import apiMixin from './Menu.api'

import uid from '../../util/uid'
import {
  dataType
} from '../../util/data/data'
import {
  unique as uniqueArray
} from '../../util/data/array'

export default {
  name: 'Menu',

  render,

  mixins: [baseMixin, formMixin, apiMixin],

  store,

  components: {
    'input-box': Input,
    btn: Btn,
    motion: Motion,
    icon: Icon,
    scroller: Scroller
  },

  props: {
    store: Object,

    ban: {
      type: Boolean,
      default: false
    },

    noTrig: {
      type: Boolean,
      default: false
    },

    noCoverTrig: {
      type: Boolean,
      default: false
    },

    height: {
      type: [Number, String],
      default: 'auto',
      validator(val) {
        if (typeof val === 'number') {
          return true
        } else if (val === 'auto') {
          return true
        } else {
          return false
        }
      }
    },

    width: {
      type: [String, Number],
      default: 170,
      validator(val) {
        if (typeof val === 'number') {
          return true
        } else if (val === 'auto') {
          return true
        } else {
          return false
        }
      }
    },

    trigHeight: {
      type: [String, Number],
      default: 'auto',
      validator(val) {
        if (typeof val === 'number') {
          return true
        } else if (val === 'auto') {
          return true
        } else {
          return false
        }
      }
    }
  },

  data() {
    this.compName = 'menu' // 
    this.uid = '' // 
    this.togglingMenu = false // 300ms 

    return {
      focusing: false, //  focus 
      clicking: false, // 
      menuHeight: 0, // 
      menuWidth: 0, // 
      panelDisplay: false, // 
      triggerHeight: 0 // 
    }
  },

  computed: {
    cPrefix() { // 
      return `${this.compPrefix}-menu`
    },

    me() {
      return this
    },

    menuClass() { //  class 
      let classArr = [
        this.cPrefix,
        this.xclass(this.compClass)
      ]

      return classArr
    }
  },

  watch: {
    deviceSize(val) {
      this._changeByDeviceSize(val)
    }
  },

  methods: {
    _initComp() {
      this.triggerHeight = this.trigHeight === 'auto' ? this.$refs.trigger.offsetHeight : this.trigHeight
    },

    /**
     * 
     */
    _binder() {
      this.$refs.scroller.$on('scrollerChange', (opt) => {
        if (this.panelDisplay) {
          this.spread()
        }

        return this.$emit('scrollerChange', {
          ...opt,
          emitter: this
        })
      })

      this.$refs.motion.$on('afterEnter', () => {
        this.$emit('afterSpread', {
          emitter: this
        })
      })

      this.$refs.motion.$on('afterLeave', () => {
        this.panelDisplay = false

        this.$emit('afterFold', {
          emitter: this
        })
      })

      if (!this.noTrig) {
        this.$refs.triggerBtn.$on('keyEnter', ({
          event
        }) => {
          this.click(event)
        })
      }
    },

    /**
     * 
     */

    /**
     * 
     */
    _adjustTriggerPoiStyle(cb) {
      this.triggerHeight = this.trigHeight === 'auto' ? this.$refs.trigger.offsetHeight : this.trigHeight

      return cb && cb()
    },

    /**
     * 
     */
    _changeByDeviceSize(size) {
      return this._adjustTriggerPoiStyle()
    }
  },

  created() {
    this.uid = uid()

    this.$store.dispatch(compStore.common.add, {
      vm: this,
      name: this.compName,
      id: this.uid
    })
  }
}



